<script>
import Vue from "vue";
import Vue2Filters from "vue2-filters";
Vue.use(Vue2Filters);

import moment from "moment";

import ImageInput from "@/components/common/ImageInput.vue";

import { required, email, max } from "vee-validate/dist/rules";
import * as rules from "vee-validate/dist/rules";
var x = require("vee-validate/dist/rules");
import { mapActions, mapGetters } from "vuex";
import {
  extend,
  ValidationObserver,
  ValidationProvider,
  setInteractionMode
} from "vee-validate";

extend("databr", {
  validate(value) {
    var date = moment(value, 'DD/MM/YYYY', true);
    return date.isValid();
  },
  message: "{_field_} invlida."
})

import CpfValidator from "@/plugins/cpf-validator";
extend("cpf", {
  ...CpfValidator,
  message: "CPF invlido."
});

setInteractionMode("eager");

extend("required", {
  ...required,
  message: "{_field_} no pode estar vazio."
});
extend("max", {
  ...max,
  message: "{_field_} campo no pode ter mais do que {length} caracteres."
});
extend("email", {
  ...email,
  message: "E-mail precisa ser vlido."
});
extend("min", {
  validate(value, args) {
    return value.length >= args.length;
  },
  params: ["length"],
  message: "{_field_} deve ter pelo menos {length} caracteres."
});

export default {
  components: {
    ValidationProvider,
    ValidationObserver,
    ImageInput
  },
  data: () => ({
    profissaoItens: [
      { cod: 1, name: "No estou trabalhando" },
      { cod: 2, name: "Aposentado INSS" },
      { cod: 3, name: "Pensionista INSS" },
      { cod: 4, name: "Aposentado (outros)" },
      { cod: 5, name: "Assalariado" },
      { cod: 6, name: "Autnomo" },
      { cod: 7, name: "Empresrio" },
      { cod: 8, name: "Funcionrio Pblico" },
      { cod: 9, name: "Militar" }
    ],
    error: {
      cpf: false
    },
    isSenhaRequired: false,
    show: false,
    enviar: false,
    msgImagens:
      "Por favor, preencha as imagens dos documentos listados acima, respeitando o limite de 12mb por imagem.",
    scannerOn: false,
    showVideo: false,
    videoPlaying: false,
    videoEnded: false,
    videoReady: false,
    uploadFilesOn: false,
    finishOn: false,
    valid: false,
    menu: false,
    status: 0,
    progressMsg: `Estamos consultando as formas de crdito disponvel para voc,
                  este processo pode levar at 2 minutes. Aguarde.`,
    progressValue: 1,
    progressNextCheckPoint: 25,
    progressTimer: false,
    valor_pre_aprovado: "0,00",
    success: "#00Fe02",
    info: "#ffff",
    fileFrente: null,
    fileVerso: null,
    fileComprovanteResidencial: null,
    fileComprovanteBancario: null,
    messageFrente: "",
    messageVerso: "",
    messageComprovanteResidencial: "",
    messageComprovanteBancario: "",
    fileFrenteOk: false,
    fileVersoOk: false,
    fileComprovanteResidencialOk: false,
    fileComprovanteBancarioOk: false,
    uploading: false,
    documentos: [
      { cod: 1, name: "CNH" },
      { cod: 2, name: "RG com Nmero de CPF " },
      { cod: 3, name: "Carteira de Trabalho com CPF" },
      { cod: 4, name: "Carteira Funcional" }
    ],
    // tipoDocumentoText: "",
    allowedTypes: ["image/jpeg", "image/jpg", "image/png"],
    images: new FormData(),
    dialog: false,
    urlName: window.location.hostname || "",
    laranja: "#ED9100",
    disabled: false,
    maxDate: moment(new Date())
      .subtract(18, "years")
      .format("YYYY-MM-DD"),
    minDate: moment(new Date())
      .subtract(101, "years")
      .format("YYYY-MM-DD")
  }),

  methods: {
    ...mapActions([
      "updateStatusResolve",
      "updateLeadForm",
      "uploadFileForm",
      "uploadImagem",
      "getLeadForm",
      "updateForm",
      "getIp"
    ]),
    
    playVideo() {
      this.videoPlaying = true;
      this.$refs.youtube.player.playVideo()
    },

    onVideoEnd() {
      console.log('video.ended');
      this.videoPlaying = false;
      setTimeout(()=> {
        this.videoEnded = true;
      }, 1000);
    },

    onVideoReady() {
      console.log('video.ready');
      this.videoReady = true;
      if ( this.scannerOn ) {
        this.playVideo();
      }
    },

    onVideoPaused() {
      console.log('video.paused');
    },

    sendDados() {
      this.$refs.observer.validate().then(retorno => {
        if (retorno) {
          if (this.leadForm.profissao == 2 || this.leadForm.profissao == 3) {
            window.fbq("track", "Lead");
          }
          this.leadForm.statusCadastro = 2;
          this.updateLeadForm(this.leadForm)
            .then(result => {
              this.disabled = true;
              this.scannerOn = true;
              if ( this.videoReady ) {
                this.playVideo();
              }
              this.checkProgressTimer();
              this.$socket.emit("request-scanner", {
                from: this.$socket.id,
                id: this.leadForm.id,
                leadId: this.leadForm.id,
                cpf: this.leadForm.cpf,
                senha: this.leadForm.senhaINSS
              });
            })
            .catch(err => {
              console.log(err);
            });
        }
      });
    },
    emitProgressView(progressViewValue) {
      this.$socket.emit("update-front-status", {
        socketId: this.$socket.id,
        progressView: progressViewValue,
        leadId: this.$route.query.j2
      });
    },
    onContratar() {
      this.uploadFilesOn = true;
    },

    sendUploadFile() {
      this.enviar = true;
      if (this.filesOk) {
        this.salvarImagens();
      }
    },
    upImagem(campo, file) {
      return this.uploadImagem({
        id: this.leadForm.id,
        campo: campo,
        file: file
      });
    },
    salvarImagens() {
      window.fbq("track", "SubmitApplication");

      // this.images.append("file", this.fileFrente);
      // this.images.append("file", this.fileVerso);
      // this.images.append("file", this.fileComprovanteResidencial);
      // this.images.append("file", this.fileComprovanteBancario);
      this.uploading = true;
      var promisses = [
        this.upImagem("documentofoto1", this.fileFrente),
        this.upImagem("comprovanteResiFoto", this.fileComprovanteResidencial),
        this.upImagem("comprovanteBancFoto", this.fileComprovanteBancario)
      ];
      if ( this.fileVerso ) promisses.push(this.upImagem("documentofoto2", this.fileVerso));

      Promise.all(promisses).then((response)=> {
        return this.updateLeadForm({
          statusCadastro: 5,
          tipoDocumento: this.leadForm.tipoDocumento,
          id: this.leadForm.id
        })
        .then(result => {
          this.finishOn = true;
          this.uploading = false;
          this.emitProgressView(100);
        })
      }).catch(err => {
        this.uploading = false;
        console.log(err);
        // TODO: Tratar Erro!!
      });
    },

    onChangeDocumento: function(el) {
      this.images = [];
      this.images = new FormData();
    },

    onSelectFrente() {
      console.log(this.fileFrente + " :fileFrente");
      

      if (!this.allowedTypes.includes(this.fileFrente.type)) {
        return (
          (this.messageFrente = "O tipo de arquivo est errado."),
          (this.fileFrenteOk = false)
        );
      }
      if (this.fileFrente.size > 12582912) {
        return (
          (this.messageFrente =
            "Imagem muito grande, o tamanho mximo permitido  12mb."),
          (this.fileFrenteOk = false)
        );
      }
      this.messageFrente = "";
      this.fileFrenteOk = true;
      console.log(this.messageFrente + " :messageFrente");
    },

    onSelectVerso() {
      

      if (!this.allowedTypes.includes(this.fileVerso.type)) {
        return (
          (this.messageVerso = "O tipo de arquivo est errado."),
          (this.fileVersoOk = false)
        );
      }
      if (this.fileVerso.size > 12582912) {
        return (
          (this.messageVerso =
            "Imagem muito grande, o tamanho mximo permitido  12mb"),
          (this.fileVersoOk = false)
        );
      }
      this.messageVerso = "";
      this.fileVersoOk = true;
    },

    onSelectComprovanteResidencial() {

      if (!this.allowedTypes.includes(this.fileComprovanteResidencial.type)) {
        return (
          (this.messageComprovanteResidencial =
            "O tipo de arquivo est errado."),
          (this.fileComprovanteResidencialOk = false)
        );
      }
      if (this.fileComprovanteResidencial.size > 12582912) {
        return (
          (this.messageComprovanteResidencial =
            "Imagem muito grande, o tamanho mximo permitido  12mb."),
          (this.fileComprovanteResidencialOk = false)
        );
      }
      this.messageComprovanteResidencial = "";
      this.fileComprovanteResidencialOk = true;
    },

    onSelectComprovanteBancario() {


      if (!this.allowedTypes.includes(this.fileComprovanteBancario.type)) {
        return (
          (this.messageComprovanteBancario = "O tipo de arquivo est errado."),
          (this.fileComprovanteBancarioOk = false)
        );
      }
      if (this.fileComprovanteBancario.size > 12582912) {
        return (
          (this.messageComprovanteBancario =
            "Imagem muito grande, o tamanho mximo permitido  12mb."),
          (this.fileComprovanteBancarioOk = false)
        );
      }
      this.messageComprovanteBancario = "";
      this.fileComprovanteBancarioOk = true;
    },

    onTentarNovamente() {
      this.sendDados();
    },

    // CREIO QUE S SERVIA PRO DATE-PICKER DENTRO DE <V-MENU>
    // save(date) {
    // this.$refs.menu.save(date);
    // },

    changeProfissao(value) {
      this.leadForm.profissao = value;
      if (this.leadForm.profissao == 2 || this.leadForm.profissao == 3) {
        window.fbq("track", "AddToWishlist");
      }
    },
    /**
     * Cria timer para incrementar progressbar de checkpoint em checkpoint para no pular de 5 para 25% direto
     */
    checkProgressTimer() {
      if ( this.progressTimer ) clearInterval(this.progressTimer);
      this.progressTimer = setInterval(()=> {
        if ( this.progressValue < this.progressNextCheckPoint - 1 ) {
          this.progressValue++
        }
        else if ( this.progressTimer ) {
          clearInterval(this.progressTimer);
        }
      }, 1200);
    }
  },

  computed: {
    ...mapGetters(["leadForm"]),
    computedDateFormatted() {
      return this.leadForm.dataNascimento
        ? this.$moment(this.leadForm.dataNascimento).format("L")
        : "";
    },
    filesOk() {
      return (
        this.fileFrente &&
        this.fileComprovanteResidencial &&
        this.fileComprovanteBancario &&
        Object.keys(this.messageFrente).length === 0 &&
        Object.keys(this.messageComprovanteResidencial).length === 0 &&
        Object.keys(this.messageComprovanteBancario).length === 0 &&
        (this.leadForm.tipoDocumento === 1 ||
          (this.fileVerso && Object.keys(this.messageVerso).length === 0))
      );
    },
    tipoDocumentoText() {
      if (this.leadForm && this.leadForm.tipoDocumento) {
        return this.documentos.filter(s => {
          return s.cod == this.leadForm.tipoDocumento;
        })[0].name;
      }
      return "";
    }
  },

  watch: {
    menu(val) {
      val && setTimeout(() => (this.$refs.picker.activePicker = "YEAR"));
    }
  },

  sockets: {
    "scanner-response": function(data) {
      /**
       * recebe os dados do scanner
       */
      this.status = parseInt(data.status);
      if (this.status == 200) {
        this.scannerOn = true;
        this.valor_pre_aprovado = parseFloat(
          data.details && data.details.calculated ? data.details.calculated.valorPreAprovado : data.valorPreAprovado
        ).toFixed(2);
        if (this.valor_pre_aprovado > 0) {
          this.isSenhaRequired = true;
          window.fbq("track", "InitiateCheckout");
        }
      }
    },
    "scanner-status": function(data) {
      if (data) {
        console.log(data);
        this.scannerOn = true;
        this.progressMsg = data.msg;
        if ( this.progressTimer ) {
          clearInterval(this.progressTimer);
        }
        this.progressValue = data.progress;
        this.progressNextCheckPoint = data.nextCheckPoint;
        this.checkProgressTimer();
        this.emitProgressView(data.progress);
      }
    },
    connected: async function(val) {
      /**
       * Registra o socket no server
       */
      await this.$socket.emit("register-lead", {
        socketId: val,
        leadId: this.$route.query.j2
      });
    }
  },

  created() {
    /**
     * TODO: fazer uma tratativa para se nao houver id ele nao entrar na pagina.
     */
    console.log('query', this.$route.query);
    
    this.getLeadForm({
      id: this.$route.query.j2,
      token: this.$route.query.j1
    }).then(result => {
      /**
       * retorno do scanner ja concluido
       */
      if (result.statusCadastro == 4 || result.statusCadastro == 3 && result.scannerResults.length > 0 && result.statusScanner === 200) {
        this.status = parseInt(result.scannerResults[0].status);
        this.scannerOn = true;
        this.valor_pre_aprovado = parseFloat(
          result.scannerResults[0].details.calculated.valorPreAprovado
        ).toFixed(2);

        this.fileFrente = result.documentofoto1
          ? new File(["foo"], result.documentofoto1, {
              type: "text/plain"
            })
          : "";
        this.fileVerso = result.documentofoto2
          ? new File(["foo"], result.documentofoto2, {
              type: "text/plain"
            })
          : "";
        this.fileComprovanteResidencial = result.comprovanteResiFoto
          ? new File(["foo"], result.comprovanteResiFoto, {
              type: "text/plain"
            })
          : "";
        this.fileComprovanteBancario = result.comprovanteBancFoto
          ? new File(["foo"], result.comprovanteBancFoto, {
              type: "text/plain"
            })
          : "";

        if (this.fileFrente) {
          this.finishOn = true;
        }
      }
      this.getIp().then(result => {
        this.leadForm.ip = result;
      });
    });
  },
  mounted() {}
};
</script>
